.header {
    min-height: 80px;
    background-color: var(--main-blue);
    box-shadow: 0 17px 12px 6px var(--main-blue);
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top: 5px;
    position: fixed;
    z-index: 999;
    width: 100%;
}

.user_picture {
    width: 50px;
}

.user {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.user p {
    color: var(--main-cream);
}

.logo img {
    padding-top: 10px;
    width: 50px;
}

.logo {
    display: flex;
    align-items: center;
    column-gap: 10px;
    text-decoration: none;
    color: var(--light-blue);
}

.logo p {
    font-size: 26px;
    border-radius: 20px;
    opacity: 0.5;
    background-color: var(--arctic-blue);
    box-shadow: 8px 0 30px 30px var(--arctic-blue);
}

.theme_switch img {
    width: 80px;
    cursor: pointer;
}

.theme_switch {
    align-self: center;
    position: relative;
}

.theme_switch:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 100%;
    top: 30%;
    background: var(--main-cream);
    color: var(--text-secondary);
    font-size: 12px;
    padding: 5px 10px;
    border: 1px solid var(--light-blue);
}